import { useCallback, useMemo, } from "react"

import { ChainId } from "../../defs/web3_defs"
import { useActiveWeb3React } from "../web3/useWeb3"
import conf from "../../lightcone_api/api_wrapper/global"

import { AuthNoKeyApi, AuthApiKeyApi, AuthHasEcdsaSigApi, AuthHasEddsaSigApi, AuthWsApi, } from '../../lightcone_api/api_wrapper'

function useApi(ApiClass: any) {

  const { chainId, active, } = useActiveWeb3React()

    const genApi = () => {
        if (!active || !chainId) {
            return undefined
        }

        let url: string = 'https://'
        switch(chainId) {
            case ChainId.MAINNET:
                url += process.env.REACT_APP_API_URL
                break
            default:
                url = 'http://' + process.env.REACT_APP_API_URL_UAT
        }

        conf.basePath = url

        return new ApiClass(conf)
    }

    const api = useMemo(genApi, [ApiClass, chainId, active])

    return api
}

export function useAuthWsApiInstance() {
    return useApi(AuthWsApi) as AuthWsApi
}

export function useAuthNoKeyApiInstance() {
    return useApi(AuthNoKeyApi) as AuthNoKeyApi
}

export function useAuthApiKeyApiInstance() {
    return useApi(AuthApiKeyApi) as AuthApiKeyApi
}

export function useAuthHasEcdsaSigApiInstance() {
    return useApi(AuthHasEcdsaSigApi) as AuthHasEcdsaSigApi
}

export function useAuthHasEddsaSigApiInstance() {
    return useApi(AuthHasEddsaSigApi) as AuthHasEddsaSigApi
}
