import { REFRESH_RATE } from "../defs/exchange_defs"

export function PromiseJob(fetchPromise: any, setFunc: any, prefix: any = 'PromiseJob') {

    let mounted = true

    const handler = () => {
        fetchPromise().then((data: any) => {
            if (mounted) {
                setFunc(data)
            } else {
                console.log(prefix + ' unmounted!')
            }
        }).catch((reason: any) => {
            console.error(reason)
        })
    }

    const cleanUp = () => {
        mounted = false
    }

    return {
        handler,
        cleanUp,
    }

}


export function PromiseJobInTimer(fetchPromise: any, setFunc: any, prefix: any = 'PromiseJobInTimer', timeout: number = REFRESH_RATE) {

    let mounted = true

    const handler = () => {

        mounted = true

        const fetchFunc = () => {
            fetchPromise().then((data: any) => {
                if (mounted) {
                    setFunc(data)
                } else {
                    console.log(prefix + ' unmounted!')
                }
            }).catch((reason: any) => {
                console.error(reason)
            })
        }

        fetchFunc()

        const id = setInterval(fetchFunc, timeout)

        return id
    }

    const cleanUp = (handler: any) => {
        mounted = false
        if (handler) clearInterval(handler)
    }

    return {
        handler,
        cleanUp,
    }

}
