import * as api from '../api'
import { AccountInfo, } from '../../defs/account_defs'
import { TickerData, DepthData, TradesData, ExchangeInfo, TokenInfo, } from '../../defs/exchange_defs'

import BigNumber from 'bignumber.js'

class AuthNoKeyApi extends api.AuthNoKeyApi {

    public async getAccountWrapper(owner: string, options?: any) {
        const raw_data = (await super.getAccount(owner, options)).data

        const info: AccountInfo = {
            accountId: raw_data.accountId,
            publicKey: raw_data.publicKey,
            owner: raw_data.owner,
            nonce: raw_data.nonce,
            fronzen: raw_data.fronzen === 'true'
        }

        return info
    }

    public async getMarketsWrapper(options?: any) {
        const raw_data = (await super.getMarkets(options)).data

        var markets: any = {}

        var pairs: any = {}
            
        raw_data.markets.forEach((item: any, index: number, array: any)=>{
            markets[item.market] = {
                'baseTokenId': item.baseTokenId, 
                'enabled': item.enabled == 'true' ? true: false, 
                'market': item.market, 
                'orderbookAggLevels': item.orderbookAggLevels, 
                'precisionForPrice': item.precisionForPrice, 
                'quoteTokenId': item.quoteTokenId, 
            }

            const market: string = item.market
            const ind = market.indexOf('-')
            const base = market.substring(0, ind)
            const quote = market.substring(ind + 1, market.length)

            if (!pairs[base]) {
                pairs[base] = {
                    tokenId: item.baseTokenId,
                    tokenList: [quote],
                }
            } else {
                pairs[base].tokenList = [...pairs[base].tokenList, quote]
            }

            if (!pairs[quote]) {
                pairs[quote] = {
                    tokenId: item.quoteTokenId,
                    tokenList: [base],
                }
            } else {
                pairs[quote].tokenList = [...pairs[quote].tokenList, base]
            }

        })

        return {
            markets,
            pairs,
        }
    }

    public async getTokensWrapper(options?: any) {
        const raw_data = (await super.getTokens(options)).data
        let tokens: any = {}
        raw_data.forEach((item: any, ind: number, arr: any) => {
            tokens[item.symbol] = item
        })

        // console.log('getTokensWrapper raw_data:', raw_data)
        return tokens
    }

    private getFeeMap(feeArr: any[], type: number = 0) {
        let feesMap: any = {}

        feeArr.forEach((item: any, index: number, array: any)=>{
            let key = ''
            switch(type) {
                case 1:
                    key = item.type
                    break
                default:
                    key = item.token
            }
            // feesMap[key] = new BigNumber(item.fee)
            feesMap[key] = item.fee
        })

        return feesMap
    }

    public async getExchangeInfoWrapper(options?: any) {
        const raw_data = (await super.getExchangeInfo(options)).data

        const info: ExchangeInfo = {
            ammExitFees: this.getFeeMap(raw_data.ammExitFees),
            chainId: raw_data.chainId,
            depositAddress: raw_data.depositAddress,
            exchangeAddress: raw_data.exchangeAddress,
            fastWithdrawalFees: this.getFeeMap(raw_data.fastWithdrawalFees),
            onchainFees: this.getFeeMap(raw_data.onchainFees, 1),
            openAccountFees: this.getFeeMap(raw_data.openAccountFees),
            transferFees: this.getFeeMap(raw_data.transferFees),
            updateFees: this.getFeeMap(raw_data.updateFees),
            withdrawalFees: this.getFeeMap(raw_data.withdrawalFees),
        }

        return info
    }

    public async getTickerWrapper(market: string[], options?: any) {
        const market_str = market.join(',')
        const raw_data = (await super.getTicker(market_str, options)).data['tickers']
        var tick_map: any = {}
        raw_data.forEach((item: any, ind: number, arr: any) => {
            const tick: TickerData = {
                date_time: new Date(parseInt(item[1])),
                base_token_amt: parseInt(item[2]),
                quote_token_amt: parseInt(item[3]),
                open: parseFloat(item[4]),
                high: parseFloat(item[5]),
                low: parseFloat(item[6]),
                close: parseFloat(item[7]),
                count: parseInt(item[8]),
                bid: parseFloat(item[9]),
                ask: parseFloat(item[10]),
            }
            tick_map[item[0]] = tick
        })

        return tick_map
    }

    private genAB(data: any[], isReverse: boolean = false) {

        var ab_arr: any[] = []
        var amtTotal = 0

        var ab_prices : any[] = []
        var ab_amtTotals :any [] = []
        
        data.forEach((item: any, ind: number, arr: any) => {
            const price = parseFloat(item[0])
            const amt = new BigNumber(item[1]).dividedBy(1e+18).toNumber()
            amtTotal += amt
            ab_arr.push({
                price: price,
                amt: amt,
                amtTotal: amtTotal,
            })
            ab_prices.push(price)
            ab_amtTotals.push(amtTotal)
        })

        if (isReverse) {
            ab_arr.reverse()
            ab_prices.reverse()
            ab_amtTotals.reverse()
        }

        return { ab_arr, ab_prices, ab_amtTotals }

    }

    public async getDepthWrapper(market: string, level: number = 2, limit: number = 50, options?: any) {
        const raw_data = (await super.getDepth(market, level, limit, options)).data
        const bids = this.genAB(raw_data['bids'])
        const asks = this.genAB(raw_data['asks'], true)

        const depth: DepthData = {
            version: parseInt(raw_data['version']),
            date_time: new Date(raw_data['timestamp']),
            bids: bids.ab_arr,
            bids_prices: bids.ab_prices,
            bids_amtTotals: bids.ab_amtTotals,
            asks: asks.ab_arr,
            asks_prices: asks.ab_prices,
            asks_amtTotals: asks.ab_amtTotals,
        }
        return depth
    }

    public async getMarketTradeWrapper(market: string, limit: number = 20, options?: any) {
        const raw_data = (await super.getMarketTrade(market, limit, options)).data
        const trades: TradesData = {
            totalNum: parseInt(raw_data['totalNum']),
            trades: raw_data['trades'],
        }
        return trades
    }

    public async getAmmPoolsWrapper(options?: any) {
        const raw_data = (await super.getAmmPools(options)).data

        let ammpools: any = {}

        let pairs: any = {}
        
        raw_data.pools.forEach((item: any) => {
            ammpools[item.name] = item
            
            const market: string = item.market

            let base = ''
            let quote = ''
            if (market.indexOf('LRCETH') === 0) {
                base = 'LRC'
                quote = 'ETH'
            } else {
                const ind = market.indexOf('-')
                const ind2 = market.lastIndexOf('-')
                base = market.substring(ind + 1, ind2)
                quote = market.substring(ind2 + 1, market.length)    
            }

            if (!pairs[base]) {
                pairs[base] = {
                    tokenId: item.tokens.pooled[0],
                    tokenList: [quote],
                }
            } else {
                pairs[base].tokenList = [...pairs[base].tokenList, quote]
            }

            if (!pairs[quote]) {
                pairs[quote] = {
                    tokenId: item.tokens.pooled[1],
                    tokenList: [base],
                }
            } else {
                pairs[quote].tokenList = [...pairs[quote].tokenList, base]
            }

        })

        // console.log('ammpools:', ammpools)
        // console.log('pairs:', pairs)

        return {
            ammpools,
            pairs,
        }
        
    }

    public async getAmmPoolsBalancesWrapper(options?: any) {
        const raw_data: any = (await super.getAmmPoolsBalances(options)).data

        // console.log('getAmmPoolsBalancesWrapper:', raw_data)

        let ammpoolsbalances: any = {}
        
        raw_data.forEach((item: any) => {

            let tempPooled: any = {}

            item.pooled.forEach((item2: any) => {
                tempPooled[item2.tokenId] = item2
            })

            item.pooledMap = tempPooled

            ammpoolsbalances[item.poolName] = item
        })

        return ammpoolsbalances
        
    }

}

export { AuthNoKeyApi }
